<script>
	import { Prepare,SubscribeAdd,SubscribeGet,SubscribeEventUpdate,SubscribeDetailPrepare,SubscribeDetailAdd,SubscribeDetailUpdate,SubscribeDetailDelete } from '../../config/apiSj'
	import { getEventByDateApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn,isValidMoneyLength,isValidDecimal,event_type_mother_fund,event_type_self_fund, } from '../../config/mUtils'
	import { format_money } from '../../config/mUtils'
	export default {
		data() {
			return {
				id: '',
				type: 1,//1代表母基金模块；2代表自管基金模块
				is_add_page: 1,
				fundName:'',
				count:'',//批次查看时用
				is_delete:null,

				currentIndex: -1,

				add_total: [
					{
						event: '',
						end_date: '',
						scale: '',
						should_pay_money: '',
						have_pay_money: '',
						note: '',
						event_count: '',
						unit: '',
					}
				],
				// 现金流总数据和详情数据开始
//				datalistTotal: [],
				datalistDetail: [],

				// 弹出框内编辑字段结束
				dialogVisible: false,
				alertData:[],
				aleterAddObj:{
					isAdd:1,
				},
				// 弹出框内编辑字段开始
				alertIndex:-1,
				pay: '',
				receive_date: '',
				end_date: '',
				note: '',
				
				currentIndexDetail:-1,
				currentDetailData:null,
				
				editorEventIndex:-1,
				editorEventData:null,
				detailLineData:null,//当前编辑页面 编辑详情数据
				shouldPay:0,
				
				residual_scale: 100,//新增或者编辑缴款的时候剩余的可添加缴款百分比
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.fundName = this.$route.query.name;
			this.is_add_page = this.$route.query.is_add_page;
			this.type = this.$route.query.type;
			this.count = this.$route.query.count
			this.is_delete = this.$route.query.is_delete

			if (this.is_add_page) {
				this.getCompanyRunCashDetail();
			}else{
				this.getJkData();
				
			}
		},
		computed: {
			
		},
		mounted() {
			this.calculate()
		},
		methods: {
			//获取缴款现金流 明细
			getCompanyRunCashDetail(){
				Prepare({
					fund_id:this.id,
					difference_sign: this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload.data;
						data.forEach((element,index) => {
							element.index = index+1;
							element.receive_date = '';
							element.detail = [];
						});
						this.datalistDetail = data;
						this.residual_scale = res.data.payload.residual_scale;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//明细编辑按钮弹出框出现
			addAlter(index,row){
				// console.log(row);
				this.shouldPay = row.should_pay;
				// this.shouldPay = 100.5;
				if(this.is_add_page){
					if(row.detail.length>0){
						this.alertData = JSON.parse(JSON.stringify(row.detail));
					}else{
						this.alertData = [];
					}
					this.alertData.unshift(this.aleterAddObj);
					this.dialogVisible = true;
					this.alertIndex = index;
				}else{
					if(this.is_delete){
						this.detailLineData = row;
						this.alertIndex = index;
						this.getHisttoryList(row.investor_id);
					}else{
						dialogConfirm("info", '您无权限编辑该条数据');
					}
					
				}
			},
			//保存新增行
			addSaveDetail(row){
				if(this.pay&&this.receive_date){
					if(!isValidDecimal(this.pay)){
						dialogConfirm('info','当前实缴金额格式错误!');
						return;
					}
					if (parseFloat(this.pay) <= 0) {
						dialogConfirm('info','当期实缴金额必须大于零!');
						return;
					}
					if(isValidMoneyLength(this.pay)){
						dialogConfirm('info','当前实缴金额过大!');
						return
					}
					let count = parseFloat(this.pay) ;//新增的是实缴金额
						//循环加上已经添加的实缴金额
						this.alertData.forEach((element,index) => {
							if(!element.isAdd){
								count += parseFloat(element.pay) 
							}
						});
						if(count<=this.shouldPay){
							if(this.is_add_page){
								this.alertData.splice(1,0,{
									pay:this.pay,
									receive_date:this.receive_date,
									end_date: this.end_date,
									note:this.note,
								})
								this.initData();
								this.addClearDetail();
							}else{
								SubscribeDetailAdd({
									difference_sign: this.type,
									event_id:this.detailLineData.event_id,
									investor_id:this.detailLineData.investor_id,
									date:'2019-03-13',
									pay:this.pay,//实际缴款
									note:this.note,//备注
									end_date:this.end_date,
									receive_date:this.receive_date,//实际到账日期
								}).then(res => {
									if(res.data.msgNo == 1) {
										let data = res.data.payload;
										// console.log(data);
										dialogConfirm("success", '保存成功');
										this.addClearDetail();
										this.getHisttoryList(this.detailLineData.investor_id);
										this.getJkData();
									} else {
										dialogConfirm("info", res.data.message);
									}
								})
							}
						}else{
							dialogConfirm('info','当前实缴金额不能大于当期应缴金额!');
						}
				}else{
					if(!this.pay){
						dialogConfirm('info','当期实缴金额不能为空!');
					}else if(!this.receive_date){
						dialogConfirm('info','实际到账日期不能为空!');
					}
				}
				
			},
			//清除新增行
			addClearDetail(){
				this.pay = '';
				this.end_date = '';
				this.receive_date = '';
				this.note = '';
			},
			//编辑当前行
			editDetail(index,row){
				this.alertData[this.currentIndexDetail] =JSON.parse(JSON.stringify(this.currentDetailData))
				this.currentIndexDetail = index;
				this.currentDetailData = JSON.parse(JSON.stringify(row)) ;
				if(this.is_add_page){
					this.initData();
				}
				
			},
			//删除当前行
			deleteDetail(index,row){
				if(this.is_add_page){
					this.alertData.splice(index,1);
					this.initData();
				}else{
					SubscribeDetailDelete({
						id:row.id,
						difference_sign: this.type,
					}).then(res => {
						if(res.data.msgNo == 1) {
							let data = res.data.payload;
							this.currentIndexDetail = -1;
							dialogConfirm('success','删除成功');
							this.getHisttoryList(this.detailLineData.investor_id);
							this.getJkData();
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}
			},
			//保存编辑
			editSaveDetail(index,row){
				if(row.pay&&row.receive_date){
					if(!isValidDecimal(row.pay)){
						dialogConfirm('info','当前实缴金额格式错误!');
						return;
					}
					if (parseFloat(row.pay) <= 0) {
						dialogConfirm('info','当前实缴金额格式错误!');
						return;
					}
					if(isValidMoneyLength(row.pay)){
						dialogConfirm('info','当前实缴金额过大!');
						return
					}
					let count = parseFloat(row.pay) ;//新增的是实缴金额
						//循环加上已经添加的实缴金额
					this.alertData.forEach((element,itemIndex) => {
						if(!element.isAdd&&index!=itemIndex){
							count += parseFloat(element.pay) 
						}
					});
					if(count<=this.shouldPay){
						if(this.is_add_page){
							this.currentIndexDetail = -1;
							this.initData();
						}else{
							SubscribeDetailUpdate({
								difference_sign: this.type,
								event_id:this.detailLineData.event_id,
								investor_id:this.detailLineData.investor_id,
								date:'2019-03-13',
								pay:row.pay,//实际缴款
								note:row.note,//备注
								end_date:row.end_date,
								receive_date:row.receive_date,//实际到账日期
								id:row.id
							}).then(res => {
								if(res.data.msgNo == 1) {
									let data = res.data.payload;
									this.currentIndexDetail = -1;
									dialogConfirm('success','保存成功');
									this.getHisttoryList(this.detailLineData.investor_id);
									this.getJkData();
								} else {
									dialogConfirm("info", res.data.message);
								}
							})
						}
					}else{
						dialogConfirm('info','当前实缴金额不能大于当期应缴金额!');
					}
				}else{
					// dialogConfirm('info','存在空值!');
					if(!row.pay){
						dialogConfirm('info','当期实缴金额不能为空!');
					}else if(!row.receive_date){
						dialogConfirm('info','实际到账日期不能为空!');
					}
				}
				
			},
			//取消编辑行
			editCancelDetail(row){
				this.alertData[this.currentIndexDetail] =JSON.parse(JSON.stringify(this.currentDetailData))
				this.currentIndexDetail = -1;
			},
			//保存按钮提交数据
			addSave(){
				if(!this.datalistDetail.length>0){
					dialogConfirm('info','投资人不可为空,请先添加投资人!')
					return false;
				}
					
				if(this.add_total[0].end_date){
					// 判断应缴比例格式是否正确
					if ( Number(this.add_total[0].scale) < 0 || Number(this.add_total[0].scale) > 100 || Number(this.add_total[0].scale) != this.add_total[0].scale) {
						dialogConfirm("info", '当期应缴比例录入错误');
						return
					} 
					if(Number(this.add_total[0].scale) > Number(this.residual_scale)){
						dialogConfirm("info", '当期应缴比例不得超过'+ Number(this.residual_scale).toFixed(2)+'%');
						return
					}
					if(Number(this.add_total[0].scale) == 0){
						dialogConfirm("info", '当前有投资人未输入认缴金额,无法创建缴款');
						return
					}
					let detail = [];
					let event = {
						event:this.add_total[0].event,
						scale:parseFloat(this.add_total[0].scale)/100 ,
						note:this.add_total[0].note,
						end_date:this.add_total[0].end_date,
						fund_id:this.id,
						event_count: this.add_total[0].event_count
					}
					// console.log(this.datalistDetail);
					//处理明细数据
					this.datalistDetail.forEach((element,index) => {
						
						if(element.detail.length>0){
							element.detail.forEach(item => {
								detail.push({
									investor_id:element.investor_id,
									pay:item.pay,
									note:item.note,
									date:'2018-09-20',
									end_date:item.end_date,
									receive_date:item.receive_date,
								})
							});
						}else{
							detail.push({
								investor_id:element.investor_id,
								pay:element.pay,
								note:element.note,
								date:'2018-09-20',
								end_date:element.end_date,
								receive_date:element.receive_date,
							})
						}
					});
					// console.log(event);
					// console.log(detail);
					// return false;
					SubscribeAdd({
						fund_id:this.id,
						difference_sign: this.type,
						event:JSON.stringify(event),
						detail:JSON.stringify(detail),
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", '添加成功');
							this.cancelSave()
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}else{
					dialogConfirm('info','缴款存在空值!')
				}
				
			},
			//取消添加缴款
			cancelSave(){
				if (this.type == 1) {
					var name = 'motherFunRunDataCashFlowHome';
				} else{
					var name = 'selfAdministerFunRunDataCashFlowHome';
				}
				this.$router.push({
					name: name,
					query: {
						id: this.id,
						type: this.type
					}
				})
			},
			initData(){
				let alertData = JSON.parse(JSON.stringify(this.alertData));
				alertData.shift();
				this.datalistDetail[this.alertIndex].detail = alertData;
				if(alertData.length>0){
					// console.log(this.alertData);
					// console.log(this.datalistDetail[this.alertIndex]);
					this.datalistDetail[this.alertIndex].note = alertData[0].note;
					this.datalistDetail[this.alertIndex].receive_date = alertData[0].receive_date;
					this.datalistDetail[this.alertIndex].end_date = alertData[0].end_date ;
				}else{
					this.datalistDetail[this.alertIndex].note = '';
					this.datalistDetail[this.alertIndex].receive_date = '';
				}
				this.calculate();
			},
			//计算弹窗数据总值
			calculate(alertMethod){
				// console.log(111);
				this.add_total[0].should_pay_money = 0;
				this.add_total[0].have_pay_money = 0;

				this.datalistDetail.forEach(element => {
					// console.log(element);
					if(this.add_total[0].scale){
						element.should_pay = element.subscribe*(this.add_total[0].scale/100);
						element.should_pay =Number(element.should_pay.toFixed(6)) ;
					}else{
						element.should_pay = 0;
					}
					let pay = 0;
					element.detail.forEach(element => {
						if(!element.isAdd){
							pay = pay + parseFloat(element.pay);
						}
					});
					element.pay = pay;
					if(alertMethod){
						element.end_date = this.add_total[0].end_date;
					}
					this.add_total[0].should_pay_money = format_money((parseFloat(this.add_total[0].should_pay_money)  + parseFloat(element.should_pay)).toFixed(6)) ;
					this.add_total[0].have_pay_money =  format_money((parseFloat(this.add_total[0].have_pay_money)  + parseFloat(element.pay)).toFixed(6));

				});
			},
			
	

			//获取一笔缴款详情
			getJkData(){
				SubscribeGet({
					fund_id:this.id,
					difference_sign: this.type,
					count:this.count,
				}).then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload;
						data.event.should_pay_money =  data.event.should_pay;
						data.event.have_pay_money =  data.event.pay;
						this.add_total = [];
						this.add_total.push(data.event);
						// console.log(this.add_total);
						data.detail.forEach((element,index) => {
							element.index = index+1;
						});
						this.datalistDetail = data.detail;
						this.residual_scale = res.data.payload.residual_scale;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//去编辑事件
			toEditorEvent(index,row){
				if(this.is_delete){
					this.editorEventIndex = index;
					this.editorEventData = JSON.parse(JSON.stringify(row)) ;
				}else{
					dialogConfirm("info", '您无权限编辑该条数据');
				}
				
			},
			//去取消事件编辑
			toCancleEvent(index,row){
				// console.log(this.editorEventData);
				this.editorEventIndex = -1
				this.add_total[0] = JSON.parse(JSON.stringify(this.editorEventData)) ;
			},
			//去保存事件编辑
			toSaveEvent(index,row){
				// 判断应缴比例格式是否正确
				if ( Number(row.scale) < 0 || Number(row.scale) > 100 || Number(row.scale) != row.scale ) {
					dialogConfirm("info", '当期应缴比例录入错误');
					return
				} 
				if (Number(row.scale) == 0){
					dialogConfirm("info", '当前有投资人未输入认缴金额,无法创建缴款');
					return
				}
				if( Number(row.scale) > Number(this.residual_scale)){
					dialogConfirm("info", '当期应缴比例不得超过'+ Number(this.residual_scale).toFixed(2)+'%');
					return
				}
				SubscribeEventUpdate({
					fund_id:this.id,
					difference_sign: this.type,
					id:row.id,
					event:row.event,
					scale:row.scale/100,
					note:row.note,
					end_date:row.end_date,
					event_count: row.event_count
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", '保存成功');
						this.editorEventIndex = -1;
						this.getJkData();
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//获取历史详情
			getHisttoryList(investor_id){
				SubscribeDetailPrepare({
					count:this.count,
					investor_id:investor_id,
					difference_sign: this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload.data;
						data.map(item=>{
							item.pay = format_money(item.pay)
							return item
						})
						console.log(data);
						if(data.length>0){
							this.alertData = data;
						}else{
							this.alertData = [];
						}
						this.alertData.unshift(this.aleterAddObj);
						this.dialogVisible = true;
						// console.log(data);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取事件字段里的count值
			getEventCountData(date,index){
				if (this.is_add_page) {
					this.calculate(1)
					var event_id = '';
				} else {
					var event_id = this.add_total[index].id;
				}
				if (!date) {
					this.add_total[0].event = '';
					return
				}
				if (this.type == 1) {
					var type = 2;
				} else {
					var type = 3;
				}
				getEventByDateApi({
					type: type,
					id: this.id,
					cash_type: 1,
					date: date,
					fund_type: '',
					fund_id: '',
					event_id: event_id
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.add_total[index].event = res.data.payload.event_name;
						this.add_total[index].event_count = res.data.payload.event_count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		/*padding: 0 30px 30px 30px;*/
	}
	
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
	.submit_btn_box{
		margin: 35px 0 20px 0;
	}
	.submit_btn_box button{
		height: 36px;
		margin: 0 10px;
		line-height: 36px;
		padding: 0 25px;
		color: #6292F4;
		font-size: 14px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
</style>


